// metadata_model.js
import { reactive, watch } from 'vue'

class MetadataModel {
  constructor() {
    if (MetadataModel.instance) {
      return MetadataModel.instance;
    }

    /**
     *  Vue.observable()  data
     *  Vue 
     */
    //  localStorage 
    const storedData = localStorage.getItem('metadata');
    this.data = reactive(storedData ? JSON.parse(storedData) : {
      education: [],
      workExperience: [],
      projectExperience: [],
      personalInfo: {},
      personalSummary: ''
    });

    //  localStorage
    watch(this.data, (newData) => {
      localStorage.setItem('metadata', JSON.stringify(newData));
    }, { deep: true });

    MetadataModel.instance = this;
  }

  metaDataDescribeForType(type) {
    switch (type) {
      case 'education':
        return `
        - meta_data  title  
        - meta_data  content AI2-4, bullet_point content 
        - meta_data  from_time 
        - meta_data  to_time 
        `;
      case 'workExperience':
        return `
        - meta_data  title , 
        - meta_data  content AI2-4, bullet_point content 
        - meta_data  from_time 
        - meta_data  to_time 
        `;
      case 'projectExperience':
        return `
        - meta_data  title 
        - meta_data  content AI2-4, bullet_point content 
        - meta_data  from_time 
        - meta_data  to_time 
        `;
      case 'personalInfo':
        return `
        - meta_data  name name
        - meta_data  email email
        - meta_data  phone phone
        - meta_data  desiredPosition desiredPosition
        `;
      case 'personalSummary':
        return `
        - meta_data  content content
        - meta_data  skills skills
        `;
      default:
        return '';
    }
  }

  // type
  formatForType(type) {
    switch (type) {
      case 'education':
      case 'workExperience':
      case 'projectExperience':
        return JSON.stringify({
          "title": "",
          "from_time": "",
          "to_time": "",
          "content": [
            {
              "bullet_point": "1",
              "content": "1"
            },
            {
              "bullet_point": "2",
              "content": "2"
            },
            {
              "bullet_point": "3",
              "content": "3"
            },
          ],
        });
      case 'personalInfo':
        return JSON.stringify({
          "name": "",
          "email": "",
          "phone": "",
        });
      case 'personalSummary':
      default:
        return '{}';
    }
  }

  /**
   *  type 
   *  this.data[type]
   */
  contentForType(type, title = null) {
    switch (type) {
      case 'education':
        return title
          ? this.data.education.find(item => item.title === title)
          : this.data.education;
      case 'workExperience':
        return title
          ? this.data.workExperience.find(item => item.title === title)
          : this.data.workExperience;
      case 'projectExperience':
        return title
          ? this.data.projectExperience.find(item => item.title === title)
          : this.data.projectExperience;
      case 'personalInfo':
        return this.data.personalInfo;
      case 'personalSummary':
        return this.data.personalSummary;
      default:
        return null;
    }
  }

  /**
   *  type  title 
   */
  deleteContentForTitle(type, title) {
    switch (type) {
      case 'education': {
        this.data.education = this.data.education.filter(item => item.title !== title);
        break;
      }
      case 'workExperience': {
        this.data.workExperience = this.data.workExperience.filter(item => item.title !== title);
        break;
      }
      case 'projectExperience': {
        this.data.projectExperience = this.data.projectExperience.filter(item => item.title !== title);
        break;
      }
      default:
        break;
    }
  }

  /**
   * setContentForType
   * 
   */
  setContentForType(type, content, title = null) {
    switch (type) {
      case 'education': {
        if (title) {
          const existing = this.data.education.find(item => item.title === title);
          if (existing) {
            existing.content = content;
          } else {
            this.data.education.push({ title, content });
          }
        } else {
          this.data.education.push({ content });
        }
        break;
      }
      case 'workExperience': {
        if (title) {
          const existing = this.data.workExperience.find(item => item.title === title);
          if (existing) {
            existing.content = content;
          } else {
            this.data.workExperience.push({ title, content });
          }
        } else {
          this.data.workExperience.push({ content });
        }
        break;
      }
      case 'projectExperience': {
        if (title) {
          const existing = this.data.projectExperience.find(item => item.title === title);
          if (existing) {
            existing.content = content;
          } else {
            this.data.projectExperience.push({ title, content });
          }
        } else {
          this.data.projectExperience.push({ content });
        }
        break;
      }
      case 'personalInfo':
        this.data.personalInfo = content;
        break;
      case 'personalSummary':
        this.data.personalSummary = content;
        break;
      default:
        break;
    }
  }


  /**
   * setContentForTitle
   *  content
   */
  setContentForTitle(title, content) {
    const types = ['education', 'workExperience', 'projectExperience'];
    for (const type of types) {
      const arr = this.data[type];
      if (!arr || !Array.isArray(arr)) continue;
      //  title 
      for (let i = 0; i < arr.length; i++) {
        if (arr[i].title === title) {
          arr[i].content = content;
          return;
        }
      }
    }
  }

  clearMetadata() {
    // Clear the localStorage
    localStorage.removeItem('metadata');

    // Reset the data to its initial state
    this.data.education = [];
    this.data.workExperience = [];
    this.data.projectExperience = [];
    this.data.personalInfo = {};
    this.data.personalSummary = '';

    // Optionally, you could log this or return a confirmation
    console.log("Metadata cleared and state reset to default.");
  }
}

// 
const metadataInstance = new MetadataModel();
Object.freeze(metadataInstance); // 

export default metadataInstance
