// chatgpt_model.js

import axios from 'axios'
import { reactive, watch } from 'vue'
import metadata_model from './metadata_model.js'

const ChatgptModel = (function () {
  let instance

  //  API Key
  let userApiKey = ''

  function createInstance() {
    const data = reactive({
      conversations: {}
    })

    //  localStorage 
    const storedConversations = localStorage.getItem('conversations')
    if (storedConversations) {
      try {
        Object.assign(data.conversations, JSON.parse(storedConversations))
      } catch (error) {
        console.error(':', error)
      }
    }

    //  conversations  localStorage
    watch(
      () => data.conversations,
      (newConversations) => {
        localStorage.setItem('conversations', JSON.stringify(newConversations))
      },
      { deep: true }
    )

    function initConversation(type, title) {
      if (!data.conversations[type]) {
        data.conversations[type] = {}
      }
      if (!data.conversations[type][title]) {
        data.conversations[type][title] = [
          {
            text: `

${describeForSenderMessage()}

  

: ${JSON.stringify(metadata_model.contentForType(type, title))}
: ${JSON.stringify(metadata_model.contentForType('personalInfo', ''))}
: ${JSON.stringify(metadata_model.metaDataDescribeForType('personalInfo'))}

  
AI 2-4 /: JSON. 

  
1.   

2.   

 STAR   
 - Situation
- Task
- Action
- ResultXX%XX
3.   
XXXX
4.   
AI + is_enough  trueAIAI true true
true
5. 

6.   


  
 JSON  messagemeta_data2-4is_enough

    {
      "message": String,
      "meta_data": {
        "resumeData": ${metadata_model.formatForType(type)}  
      },
      "is_enough": Boolean,
      "prompt_hint": [...]
    }

-message
-is_enoughAI trueAI false
-meta_datais_enoughfalseresumeDatais_enoughtrueresumeDatamessage
-prompt_hintneed_prompt_hinttrue33
meta_data: ${metadata_model.metaDataDescribeForType(type)}
 + 
10A/B20%
    `,
            sender: 'system',
          },
        ]
      }
    }

    function getPromptForType(type, title) {
      return data.conversations[type][title][0].text
    }

    function getMessagesForTitle(type, title) {
      initConversation(type, title)
      return data.conversations[type][title]
    }

    function addMessage(type, title, message) {
      data.conversations[type][title].push(message)
    }

    async function sendMessage(type, title, userText, display = true, needPromptHint = false) {
      if (!userText.trim()) return

      const gptText = JSON.stringify({
        user_text: userText,
        need_prompt_hint: needPromptHint,
      });

      data.conversations[type][title].push({
        text: gptText,
        sender: 'me',
        display: display,
      })

      const gptReply = await fetchGptResponse(type, title, gptText)
      data.conversations[type][title].push({
        text: gptReply,
        sender: 'gpt',
        display: true,
      })
    }

    function describeForSenderMessage() {
      return `
        user_text: 
        need_prompt_hint: truemeta_dataprompt_hint, , 3
      `;
    }

    function buildGptMessagesFromData(type, title) {
      return data.conversations[type][title].map((msg) => {
        let role = 'system'
        if (msg.sender === 'me') {
          role = 'user'
        } else if (msg.sender === 'gpt') {
          role = 'assistant'
        }
        return { role, content: msg.text }
      })
    }

    function setApiKey(key) {
      userApiKey = key
    }

    function clearConversations() {
      data.conversations = {}
    }

    // :  GPT  userApiKey ()
    async function fetchGptResponse(type, title, userText) {
      const apiUrl = 'https://api.openai.com/v1/chat/completions'

      //  userApiKey  key
      // 
      const apiKey = userApiKey || ''

      try {
        let messages = buildGptMessagesFromData(type, title)
        messages.push({ role: 'user', content: userText })

        const response = await axios.post(
          apiUrl,
          {
            model: 'gpt-4o', //  model
            messages,
            temperature: 0.7,
          },
          {
            headers: {
              'Content-Type': 'application/json',
              Authorization: `Bearer ${apiKey}`,
            },
          }
        )

        const gptContent = response.data.choices[0].message.content.trim()
        return gptContent
      } catch (error) {
        console.error(' GPT API ', error)
        return 'GPT  API Key '
      }
    }

    return {
      getMessagesForTitle,
      getPromptForType,
      sendMessage,
      setApiKey,
      addMessage,
      clearConversations,
    }
  }

  return {
    getInstance() {
      if (!instance) {
        instance = createInstance()
      }
      return instance
    },
  }
})()

export default ChatgptModel